import './App.css';
import { BrowserRouter , Routes , Route  } from 'react-router-dom'
import Login from './component/Login';
import Signin from './component/Signin';

function App() {
  return (
  
      <BrowserRouter>
        <Routes>
            <Route path="/login" exact element={Login} />
            <Route path="/signin" element={Signin}  />
        </Routes>
      </BrowserRouter>
  );
}

export default App;
